import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Home from './screens/Home';
import AnimalDetail from './screens/AnimalDetail';
import LoginPage from './screens/Login';
import AnimaisDisponiveis from './screens/AnimalsList';
import AddAnimalPage from './screens/AddAnimalPage';
import ProtectedRoute from './components/ProtectedRoute';
import UserProfile from './screens/UserProfile';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import RegisterUser from './screens/RegisterUser';
import ForgotPassword from './screens/ForgotPassword';
import ResetPassword from './screens/ResetPassword';

const App = () => {
  // Estados para armazenar se o usuário está logado e o nome do usuário
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userName, setUserName] = useState('');

  // Verifica se há um token armazenado no localStorage ao carregar o componente
  useEffect(() => {
    const token = localStorage.getItem('authToken');
    const storedUserName = localStorage.getItem('userName');

    if (token && storedUserName) {
      // Se o token e o nome do usuário estiverem disponíveis, o usuário está logado
      setIsLoggedIn(true);
      setUserName(storedUserName);
    }
  }, []); // Executa apenas uma vez ao montar o componente

  const handleLogin = (token, userName) => {
    // Função chamada ao fazer login, salvando o token e nome no localStorage
    localStorage.setItem('authToken', token);
    localStorage.setItem('userName', userName);
    setIsLoggedIn(true); // Atualiza estado para indicar que o usuário está logado
    setUserName(userName); // Define o nome do usuário
  };

  const handleLogout = () => {
    // Função de logout, remove o token e nome do usuário
    localStorage.removeItem('authToken');
    localStorage.removeItem('username');
    localStorage.removeItem('token');
    localStorage.removeItem('idUsuario');
    localStorage.removeItem('userName');

    setIsLoggedIn(false); // Define o estado como deslogado
    setUserName(''); // Limpa o nome do usuário
  };

  return (
    <Router>
      <Navbar isLoggedIn={isLoggedIn} userName={userName} onLogout={handleLogout} />
      <div>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/Home" element={<Home />} />
          <Route path="/animais/:id" element={<AnimalDetail />} />
          <Route path="/esqueci-minha-senha" element={<ForgotPassword />} />
          <Route path="/redefinir-senha/:token" element={<ResetPassword />} />
          <Route path="/AnimaisDisponiveis" element={<AnimaisDisponiveis />} />
          <Route
            path="/AdicionarAnimal"
            element={
              <ProtectedRoute>
                <AddAnimalPage />
              </ProtectedRoute>

            }
          />
          <Route path="/perfil" element={<UserProfile />} />
          <Route path="/cadastre-se" element={<RegisterUser />} />
          <Route path="/login" element={<LoginPage onLogin={handleLogin} />} />

        </Routes>
      </div>
      <Footer />
    </Router>
  );
};

export default App;
