import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import '../styles/UserProfile.css'; // Estilos personalizados para o perfil do usuário
require('dotenv').config();
const REACT_APP_BACKURL = process.env.REACT_APP_BACKURL;

const UserProfile = () => {
    const [userData, setUserData] = useState(null);
    const [editing, setEditing] = useState(false);
    const [formData, setFormData] = useState({});
    const [animals, setAnimals] = useState([]); // Armazena a lista de animais
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchUserData = async () => {
            const token = localStorage.getItem('authToken');
            const userId = localStorage.getItem('idUsuario'); // Obtém o idUsuario do localStorage

            try {
                // Primeira chamada para buscar o perfil do usuário
                const userResponse = await axios.get(`${REACT_APP_BACKURL}/user/profile`, {
                    headers: { Authorization: `Bearer ${token}` }
                });
                setUserData(userResponse.data);
                setFormData(userResponse.data);

                // Segunda chamada para buscar apenas os animais do usuário logado usando o novo endpoint
                const animalsResponse = await axios.get(`${REACT_APP_BACKURL}/animais/usuario/${userId}`, {
                    headers: { Authorization: `Bearer ${token}` }
                });

                // Adiciona o prefixo Base64 às imagens dos animais
                const animalsWithBase64Images = animalsResponse.data.map(animal => {
                    if (animal.imagem) {
                        animal.imagem = `data:image/jpeg;base64,${animal.imagem}`;
                    }
                    return animal;
                });

                setAnimals(animalsWithBase64Images); // Define os animais com imagens ajustadas
                setLoading(false);
            } catch (error) {
                console.error('Erro ao buscar dados do usuário:', error);
                setError('Falha ao carregar dados do usuário.');
                setLoading(false);
            }
        };

        fetchUserData();
    }, []);

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSave = async () => {
        const token = localStorage.getItem('authToken');
        try {
            const response = await axios.put(`${REACT_APP_BACKURL}/user/profile`, formData, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setUserData(response.data);
            localStorage.setItem('userName', response.data.nome);
            setEditing(false);
            navigate('/perfil');
        } catch (error) {
            console.error('Erro ao atualizar perfil:', error);
            setError('Falha ao atualizar perfil.');
        }
    };

    const handleDeleteAccount = async () => {
        const token = localStorage.getItem('authToken');
        try {
            await axios.delete(`${REACT_APP_BACKURL}/user/delete`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            localStorage.removeItem('authToken');
            navigate('/');
        } catch (error) {
            console.error('Erro ao excluir conta:', error);
            setError('Falha ao excluir a conta.');
        }
    };

    const handleDeleteAnimal = async (animalId) => {
        const token = localStorage.getItem('authToken');
        try {
            await axios.delete(`${REACT_APP_BACKURL}/animais/${animalId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setAnimals(animals.filter(animal => animal.id !== animalId)); // Atualiza a lista localmente
        } catch (error) {
            console.error('Erro ao excluir animal:', error);
            setError('Falha ao excluir o animal.');
        }
    };

    const handleEditAnimal = (animal) => {
        // Aqui você pode implementar a lógica para editar o animal, talvez redirecionando para uma página de edição.
        console.log('Editar animal:', animal);
    };

    if (loading) {
        return <div>Carregando dados...</div>;
    }

    if (error) {
        return <div>{error}</div>;
    }

    return (
        <div className="user-profile-container">
            <h2>Perfil do Usuário</h2>
            {error && <p className="error-message">{error}</p>}

            {!editing ? (
                <div className="profile-details">
                    <p><strong>Nome:</strong> {userData?.nome || 'Nome não disponível'}</p>
                    <p><strong>Email:</strong> {userData?.email || 'Email não disponível'}</p>
                    <p><strong>Cidade:</strong> {userData?.cidade || 'Cidade não disponível'}</p>
                    <p><strong>Telefone:</strong> {userData?.telefone || 'Telefone não disponível'}</p>

                    <button onClick={() => setEditing(true)} className="edit-button">Editar Perfil</button>
                    <button onClick={handleDeleteAccount} className="delete-button">Excluir Conta</button>
                </div>
            ) : (
                <div className="profile-edit-form">
                    <div className="form-group">
                        <label htmlFor="nome">Nome:</label>
                        <input
                            type="text"
                            id="nome"
                            name="nome"
                            value={formData.nome || ''}
                            onChange={handleChange}
                            required
                        />
                    </div>

                    <div className="form-group">
                        <label htmlFor="email">Email:</label>
                        <input
                            type="email"
                            id="email"
                            name="email"
                            value={formData.email || ''}
                            onChange={handleChange}
                            required
                        />
                    </div>

                    <div className="form-group">
                        <label htmlFor="cidade">Cidade:</label>
                        <input
                            type="text"
                            id="cidade"
                            name="cidade"
                            value={formData.cidade || ''}
                            onChange={handleChange}
                        />
                    </div>

                    <div className="form-group">
                        <label htmlFor="telefone">Telefone:</label>
                        <input
                            type="text"
                            id="telefone"
                            name="telefone"
                            value={formData.telefone || ''}
                            onChange={handleChange}
                        />
                    </div>

                    <button onClick={handleSave} className="save-button">Salvar Alterações</button>
                    <button onClick={() => setEditing(false)} className="cancel-button">Cancelar</button>
                </div>
            )}

            <h3>Meus Animais</h3>
            {animals.length > 0 ? (
                <ul className="animal-list">
                    {animals.map(animal => (
                        <li key={animal.id} className="animal-item">
                            <img src={animal.imagem} alt={animal.nome} className="animal-image" />
                            <div className="animal-details">
                                <p><strong>Nome:</strong> {animal.nome}</p>
                                <p><strong>Espécie:</strong> {animal.especie}</p>
                                <p><strong>Raça:</strong> {animal.raca}</p>
                                <button onClick={() => handleEditAnimal(animal)} className="edit-animal-button">Editar</button>
                                <button onClick={() => handleDeleteAnimal(animal.id)} className="delete-animal-button">Excluir</button>
                            </div>
                        </li>
                    ))}
                </ul>
            ) : (
                <p>Nenhum animal cadastrado.</p>
            )}

        </div>
    );
};

export default UserProfile;
